#blog-list{
	.grid_post_container{
		display: block;

		& > div{
			margin-bottom: 40px;
		}
	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){}

@media (min-width: $breakpoints-large){
	#blog-list{
		.grid_post_container{
			display: grid;
			margin: 40px 0px;

			& > div{
				margin-bottom: 0px!important;
			}
		}
	}
}

@media (min-width: $breakpoints-xlarge){}
