.post_featured{
	background-color: $white-pure;
	width: 100%;
	position: relative;
	letter-spacing: -1px;
	box-shadow: 0px 20px 39px -5px rgba(0,0,0,0.04);
	margin-bottom: 40px;

	a{
		display: block;
	}

	.post_featured__photo{
		min-height: 500px;
		width: 100%;
		background-color: $blue-primary;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		background-image: url(../images/architecture-building-cebu-1364558.jpg);
	}
	.post_featured__details{
		@extend .color-es;
		text-align: center;
		line-height: 24px;

		.featured-details__title,
		.featured-details__desc,
		.featured-details__action{
			padding: 50px 50px 0px 50px;
		}
		.featured-details__desc{
			padding-top: 0px;
		}
		.featured-details__action{
			padding-bottom: 50px;
		}

		h2{
			font-family: $font-montserrat;
			font-weight: 800;
			margin-bottom: 28px;
			line-height: inherit;

			&:hover{
				color: $blue-primary;
			}
		}
		p{
			font-family: $font-work-sans;
			line-height: inherit;
			white-space: pre-line;
		}
	}
	@extend .post-card;

	.post-card__avatar{
		display: grid;
		grid-template-columns: repeat(12,1fr);
		margin: 0px;


		.avatar__muted-line{
			grid-column-start: 1;
    		grid-column-end: 13;
			grid-row: 1/1;
			align-self: center;
			margin: 0px;
		}
		.avatar__white-bg{
		    grid-column: 3/11;
			grid-row: 1/1;
			position: relative;

			.white-bg-container{
				background-color: white;
			    display: inline-block;
			    padding: 0px 15px;

				& > div{
					display: inline-block;
					vertical-align: middle;
				}

				.avatar__circle{
					// margin-right: 15px;
				}
				.avatar__name{

				}
			}

		}
	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){
	.post-card__avatar{
		.avatar__white-bg{
		    grid-column: 5/9;
		}
	}
}

@media (min-width: $breakpoints-large){
	.post_featured{
		display: grid;
		grid-template-columns: 2fr 1fr;
		grid-template-rows: 1fr;
		max-width: 100%!important;
	}
}

