*{
	margin: 0;
	padding: 0;
}

body{
	background-color: $white-paper;
	font-family: $font-work-sans;
}

h1, h2, h3, h4, h5, h6{
	font-family: $font-montserrat;

}

p{
	font-family: inherit;
}

h1, h2, h3, h4, h5, h6, p{
	line-height: 16px;
	letter-spacing: -1px;
}

h1{font-size: 36px;}
h2{font-size: 24px;}
h3{font-size: 19px;}
h4{}
h5{}
h6{}

a{
	text-decoration: none;
	font-family: inherit;
	font-size: inherit;
	color: inherit;
}

ul, ol{
	list-style: none;
}
