#above-the-fold{
	background-color: $blue-primary;
	background-image: url('../images/asia-bay-beach-1076240.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center bottom;
	color: $white-pure;
	position: relative;
	z-index: 1;

	&:after{
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: #000;
		opacity: 0.2;
		z-index: 2;
	}

	& > .container_wrapper{
		z-index: 3;
		position: relative;
	}

	h1#above-the-fold__description{
		line-height: 1;
		font-size: 36px;
		letter-spacing: -3px;
		text-align: center;
		padding: 80px 16px;
	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){
	#above-the-fold{
		height: 800px;

		& > .container_wrapper{
			z-index: 3;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			height: 100%;
		}
	}
}

@media (min-width: $breakpoints-large){
	h1#above-the-fold__description{
		white-space: pre-line;

	}
}

@media (min-width: $breakpoints-xlarge){}
