footer{
	color: #fff;
	background-color: $blue-primary;
	text-align: center;

	.container_wrapper{
		padding: 10% 0%;
	}

	ul li{
		line-height: 22px;
	}
	.muted{
		opacity: 0.6;
		font-size: 14px;
	}
	#footer__site_description{
		h3{
			margin-top: 14px;
			margin-bottom: 14px;
			font-size: 18px;
		}
		p{
			@extend .muted;
		}
	}
	#footer__social_media,
	#footer__pages{
		h3{
			margin-top: 32px;
			margin-bottom: 16px;
		}
		a{
			@extend .muted;
			font-weight: 300;

			&:hover{
				opacity: 1;
			}
		}
	}
	#footer__social_media{
		ul li a i{
			margin-right: 20px;
		}
	}
	#footer__bottom_menus{
		height: 70px;
		background-color: $blue-dark;
	}
}

@media (min-width: $breakpoints-small){

}

@media (min-width: $breakpoints-medium){

}

@media (min-width: $breakpoints-large){
	footer{
		text-align: left;
	}
	footer .container_wrapper {
		display: flex;
		padding: 65px 0px;
	}
	footer .container_wrapper > div{
		padding-right: 16px;
	}
	#footer__site_description{
		width: 50%;

		.__center{
			margin: 0;
		}
		h3{
			margin-bottom: -5px!important;
		}
		p{
			white-space: pre-line;
		}
	}
	#footer__social_media{
		width: 25%;
	}
	#footer__pages{
		width: 25%;

		ul{
			height: 75px;
		    width: 75%;
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
		}

	}
	#footer__social_media,
	#footer__pages{
		h3{
		    margin-top: 64px!important;
			margin-bottom: 4px!important;
		}
	}
}
