#post__single-page{
	.post__featured-image{
		background-image: url('../images/architecture-building-cebu-1364558.jpg');
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		min-height: 700px;
		display: flex;
	    flex-flow: row;
		color: $white-pure;
		margin-bottom: 55px;

		.post__title-container{
			align-self: flex-end;
			// min-height: 251px;
			min-height: 400px;

			width: 100%;
	    	text-align: center;

			// box-shadow: inset 0px -200px 200px -100px rgba(0,0,0,0.8);
			box-shadow: inset 0 -200px 250px -40px rgba(0,0,0,.8);

			display: flex;
			justify-content: flex-end;
			flex-direction: column;

			h2, p{
				line-height: 1.8rem;
			}
			& > p{
				font-size: 12px;
				// font-weight: 200;
			}

			.post__avatar-container{
				padding: 15px;

				.post__avatar a{
					width: 45px;
					height: 45px;
					border-radius: 50%;
					display: block;
					margin: 15px auto;
					background-color: $gray-muted;
					text-indent: -9999px;
				}
				.post__avatar-name{
					h5{
						font-weight: 400;
					}

				}
			}

		}
	}

	.post__content{
		max-width: 785px;
		width: 100%;

		* {
			line-height: 1.5rem;
			color: $blue-dark;
		}

		a {
			color: $blue-primary;
		}

		p{
			margin-bottom: 15px;
		}

		h1, h2, h3, h4, h5, h6{
			margin: 25px 0px;
		}

		.post__media_container{
			.post__media_photo{
				background-color: $blue-primary;
				background-image: url('../images/adventure-albay-clouds-672358.jpg');
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center center;
				min-height: 450px;
			}
			.post__media_caption p {
				text-align: center;
				font-size: 14px;
			}
		}
	}
	.post__tags{
		display: flex;
		font-size: 14px;
		color: $blue-dark;
		margin-bottom: 30px;

		h6{
			font-size: inherit;
			margin-right: 5px;
			color: inherit;
		}

		ul{
			display: flex;
			flex-wrap: wrap;
		}

		ul li {
			font-size: inherit;
			color: inherit;
			font-weight: bold;
			margin-right: 5px;

			&:hover{
				color: $blue-primary;
			}
		}
	}
	.author-bio__container{
		text-align: center;
		color: $blue-dark;
		margin: 65px 0px 125px 0px;

		& > div{
			margin-bottom: 15px;
		}

		.author-bio__avatar a{
			width: 150px;
			height: 150px;
			border-radius: 50%;
			display: block;
			margin: auto;
			background-color: $gray-muted;
			text-indent: -9999px;
		}
		.author-bio__description {
			& > *{
				margin-bottom: 25px;
				line-height: 1.5rem;
			}
			h2{
				font-size: 16px;
			}
		}
		.author-bio__social-media{
			ul{
				font-size: 24px;
				display: flex;
				justify-content: center;
			}
			ul li{
				margin: 0px 5px;
			}
		}
	}
	#post-comment{
		color: $blue-dark;
		text-align: center;

		.comment__container{
			margin-top: 70px;
			margin-bottom: 50px;

			& > div{
				margin-bottom: 15px;
			}

			.comment__avatar{
				a{
					display: block;
					width: 82px;
					height: 82px;
					border-radius: 50%;
					background-color: $gray-muted;
					text-indent: -9999px;
					margin: auto;
				}
			}
			.comment__description{
				& > *{
					line-height: 1.5rem;
				}
				h2{
					font-size: 16px;
				}
				span{
					font-size: 12px;
				}

			}
			.comment__action{
				a{
					color: $blue-primary;
				}
			}
		}
	}
	#comment-form{
		color: $blue-dark;
		text-align: center;

		.comment-form__container{
			display: flex;
			flex-direction: column;
			color: #bbc1c6;

			& > *{
				border: none;
				border-bottom: 2px solid #bbc1c6;
				padding: 20px;
				padding-left: 0px;
				background-color: inherit;
				font-size: 14px;
				font-family: $font-montserrat;
				font-weight: 500;
				outline: none;
			}

			.comment__action{
				margin: 30px 0px;
				border: 2px solid #bbc1c6;
				color: inherit;
			}
		}

	}
}


@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){
	.post__title-container{
		position: relative;

		& > p{
		    margin-bottom: 60px;
		}

		.post__avatar-container{
			position: absolute;
    		display: flex;

			.post__avatar{
				margin: 0px 15px;
			}
			.post__avatar-name{
			    align-self: center;
			}
		}
	}

	#post__single-page {
		.author-bio__container{
			display: grid;
			grid-template-rows: repeat(2,1fr);
			grid-template-columns: repeat(7, 1fr);
			text-align: left;
		}
		.author-bio__avatar{
			grid-column: 1/2;
			grid-row: 1/4;
		}
		.author-bio__description{
			grid-column: 2/8;
			grid-row: 1/3;
			margin-left: 43px;
		}
		.author-bio__social-media{
			grid-column: 6/8;
			grid-row: 2/3;
	        margin: auto 0px 0px 0px;
		}
	}

	#post-comment{
		.comment__container{
			display: flex;
			text-align: left;

			.comment__avatar{
				margin: 0px 22px;
			}
		}
	}
	#comment-form{
		.comment-form__container{
			.comment__action{
				max-width: 195px;
				width: 100%;
				padding:15px 0px!important;
				margin: 30px 0px 30px auto!important;

			}
		}
	}
}

@media (min-width: $breakpoints-large){
	.post__content{
		margin-left: 80px;
	}
	.author-bio__container,
	.post__tags,
	#post-author-bio,
	#post-comment,
	#comment-form{
		max-width: 785px;
		width: 100%;
		margin: 0px auto 0px 80px;


		h5{
			text-align: left;
		}
	}
}

@media (min-width: $breakpoints-xlarge){}
