// colors
.color-city{
	color: $yellow-green;
}
.color-es{
	color: $blue-dark;
}
.color-cape{
	color: $blue-primary;
}

// container
.container_wrapper{
	max-width: $width-max;
	width: 90%;
	margin: 0 auto;
}

// section title
.section-title{
	display: flex;
	flex-direction: row;
	justify-content: center;
	letter-spacing: -2px;
	margin-top: 7px;
	margin-bottom: 4px;
	font-size: 30px;
	padding: 60px 0px;

	& div:first-child{
		margin-right: 10px;
	}
}

// grid post container
.grid_post_container{
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	grid-gap: 22px;
	justify-items: center;
}

// button
.button{
	margin: auto;
	max-width: 200px;
	text-align: center;

	&.border-only{
		display: block;
		border: 2px solid $gray-muted;
		padding: 10px 0px;

		&:hover{
			border: 2px solid $blue-primary;
			color: $blue-primary;
		}

	}
	&.muted{
		color: $gray-muted;


	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){}

@media (min-width: $breakpoints-large){}

@media (min-width: $breakpoints-xlarge){
	.container_wrapper{
		width: 100%;
	}
}
