#latest-review{
	margin-bottom: 60px;

	.container_wrapper{
		.grid_post_container{
			display: block;

			& > div{
				margin-bottom: 40px;
			}
		}
	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){}

@media (min-width: $breakpoints-large){
	#latest-review{
		.container_wrapper{
			.grid_post_container{
				display: grid;
			}
		}
	}
}

@media (min-width: $breakpoints-xlarge){}
