.post-card{
	background-color: $white-pure;
	width: 100%;
	position: relative;
	letter-spacing: -1px;
	box-shadow: 0px 20px 39px -5px rgba(0,0,0,0.04);

	a{
		display: block;
	}

	.post-card__category{
		color: $white-pure;
		background-color: $yellow-green;
		font-family: $font-montserrat;
		font-size: 14px;
		padding: 15px;
		display: inline-block;

		position: absolute;
	}
	.post-card__featured-image{
		height: 300px;
		width: 100%;
		background-color: $blue-primary;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		background-image: url(../images/architecture-building-cebu-1364558.jpg);
	}
	.post-card__details{
		@extend .color-es;
		text-align: center;

		padding: 28px;
		line-height: 24px;

		h2{
			font-family: $font-montserrat;
			font-weight: 800;
			margin-bottom: 28px;
			line-height: inherit;

			&:hover{
				color: $blue-primary;
			}
		}
		p{
			font-family: $font-work-sans;
			line-height: inherit;
		}
	}
	.post-card__avatar{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 28px;
		font-family: $font-montserrat;
		font-weight: 700;
		font-size: 14px;

		& > div{
			margin: 0px 4.8px;
		}

		.avatar__circle{
			width: 45px;
			height: 45px;
			border-radius: 50%;
			background-color: $gray-muted;
		}
		.avatar__name{
			color: $gray-muted;
		}
		.avatar__muted-line{
			color: $gray-muted;
			border-top: 1px solid $gray-muted;
			flex-grow: 1;
			margin-left: 16px;
			opacity: 0.5;
		}
	}
}

@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){}

@media (min-width: $breakpoints-large){}

@media (min-width: $breakpoints-xlarge){
	.post-card{
		max-width: 388px;
	}
}
