#navigation_container{
	display: grid;
	grid-template-columns:: 1fr 1fr;
	grid-template-rows: 100px auto;
	grid-template-areas:
	   "branding menu-icon"
	   "menu menu";

	&.__colored{
		color: $blue-primary!important;
	}

	#branding{
		grid-area: branding;

		.logo-container.__center{
			margin: 0;
		}
	}

	label[for=menu_icon]{
		grid-area: menu-icon;
		font-size: 32px;
		text-align: right;
	}
	#menu_icon{
		display: none;

		&:checked + menu{
			display: flex;
		}
	}
	menu{
		grid-area: menu;
		display: none;
		flex-direction: column;
		padding: 0;
		margin: 0;
	    margin-bottom: 50px;

		a{
			display: block;
			padding: 8px 0px;
			font-weight: 600;
			letter-spacing: -1px;
		}
	}

	#branding,
	label[for=menu_icon]{
		align-self: center;
	}
}


@media (min-width: $breakpoints-small){}

@media (min-width: $breakpoints-medium){
	#navigation_container{
		display: flex;
		height: 100px;
		justify-content: space-between;

		label[for=menu_icon]{
			display: none;
		}
		menu{
			display: flex;
			flex-direction: row;
			align-self: center;
			margin-bottom: 0px;

			a{
				padding: 8px;
			}
		}
	}
}

@media (min-width: $breakpoints-large){}

